
body {
  background-color: #f8f8f8;
  font-family: 'Merriweather';
  color: #666666;
}
h1,h2,h3,h4,h5,h6 {
  font-family: 'Montserrat';
}
.button:hover,button:hover,input[type=button]:hover,input[type=submit]:hover {
  text-decoration: none;
  background-color: #fff;
  color: #424440;
}
form {
  position: relative;
}
form input[type=text],form input[type=password],form input[type=url],form input[type=email] {
  height: 40px;
  line-height: 40px;
  background-color: #fff;
  width: 93%;
  font-family: 'Merriweather';
  font-size: 11px;
  font-weight: 100;
  color: #888888;
  padding-left: 10px;
  padding-right: 30px;
  margin-bottom: 15px;
  outline: none;
}
form textarea {
  border: 1px solid rgba(51, 51, 51, 0.2);
  background-color: #fff;
  width: 100%;
  letter-spacing: 0.7px;
  font-family: 'Merriweather';
  font-size: 12px;
  font-weight: 100;
  font-style: italic;
  color: #333333;
  padding: 20px;
  min-height: 180px;
  outline:none;
}
form textarea::-webkit-input-placeholder {
  color: rgba(51, 51, 51, 0.9);
}
form textarea:-moz-placeholder {
  color: rgba(51, 51, 51, 0.9);
}
form textarea::-moz-placeholder {
  color: rgba(51, 51, 51, 0.9);
}
form textarea:-ms-input-placeholder {
  color: rgba(51, 51, 51, 0.9);
}
.transition {
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
/* Header Style
====================================*/
#header {
  width: 100%;
  display: block;
  position: relative;
  margin-bottom: 30px;
}
.logo {
  text-align: center;
  padding: 120px 0;
  background-size: cover;
}
.logo h1,
.logo a {
  text-decoration: none;
  font-family: 'Montserrat';
  font-size: 60px;
  font-weight: bold;
  color: #333;
  letter-spacing: -0.5px;
  margin: 0;
}
.logo p {
  font-family: 'Montserrat';
  font-size: 14px;
  padding: 0;
  margin: 0;
  color: #888888;
}
#header .menu-container {
  background-color: #ffffff;
  border-top: 1px solid #e2e2e2;
  border-bottom: 1px solid #e2e2e2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  padding-right: 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#header nav.main-nav {
  font-family: 'Montserrat';
  font-size: 12px;
  font-weight: normal;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#header nav.main-nav > ul {
  display: block;
  position: relative;
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}
#header nav.main-nav > ul > li {
  display: inline-block;
  position: relative;
  line-height: 90px;
}
#header nav.main-nav > ul > li + li {
  margin-left: 28px;
}
#header nav.main-nav > ul > li > a {
  color: #444444;
  text-transform: uppercase;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
#header nav.main-nav > ul > li > a:hover {
  text-decoration: none;
  color: #ffb661;
}
#header nav.main-nav > ul > li.menu-item-has-children > a::after {
  font-family: Fontawesome;
  font-size: 12px;
  margin-left: 10px;
}
#header nav.main-nav > ul > li > ul {
  top: 68px;
  font-family: 'Montserrat';
}
.h-search {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
  padding-right: 30px;
}
.h-search form {
  position: relative;
}
.h-search form input[type=text] {
  height: 40px;
  line-height: 40px;
  background-color: #fff;
  width: 100%;
  font-size: 11px;
  font-weight: 100;
  color: #888888;
  padding-left: 10px;
  padding-right: 30px;
  margin-bottom: 15px;
  outline: none;
  border: 1px solid #e2e2e2;
}
.h-search .button,
.h-search button,
.h-search input[type=button],
.h-search input[type=submit] {
  color: #444444;
  border: none;
  font-size: 14px;
  font-weight: bold;
  font-family: 'Merriweather';
  text-transform: uppercase;
  background-color: transparent;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  position: absolute;
  top: 38px;
  right: 5px;
  line-height: 0;
  outline: none;
}
.h-search .head-social {
  float: right;
  margin-right: 30px;
}
.h-search .head-social .socials {
  color: #444444;
  font-size: 14px;
  line-height: 90px;
}
.h-search .head-social .socials + .socials {
