}
.h-search .search_form {
  margin: 0;
  font-family: 'Montserrat';
  font-weight: 300;
  font-style: italic;
  font-size: 11px;
  float: right;
  line-height: 90px;
}
/* Content
====================================*/
.bubble-line {
  position: relative;
  height: 30px;
  margin-left: -1px;
  margin-right: -1px;
}
.bubble-line::before {
  content: '';
  height: 0;
  width: 100%;
  border-top: 1px dashed #e2e2e2;
  position: absolute;
  top: 50%;
  left: 0;
}
.bubble-line .left-shape,
.bubble-line .right-shape {
  width: 15px;
  height: 30px;
  position: absolute;
  top: 0;
}
.bubble-line .left-shape svg path,
.bubble-line .right-shape svg path {
  fill: #f8f8f8;
  stroke: #e2e2e2;
}
.bubble-line .left-shape {
  left: 0;
}
.bubble-line .right-shape {
  right: 0;
}
